import GoogleFonts from "../services/google-fonts.service";

export default angular.module('deskworks.ngValidateFontDirective', [
  GoogleFonts.name,
])

  .directive('ngValidateFont', ($q, GoogleFonts) => {
    return {
      restrict: 'A',
      scope: true,
      require: 'ngModel',
      link: ($scope, $element, $attrs, ngModel) => {

        ngModel.$asyncValidators.font = (modelValue, viewValue) => {
          if (viewValue === 'Optima') return $q.when(); // local font
          return GoogleFonts.get({ family: viewValue });
        };
      },
    };
  });
