import template from './white-label-links.tpl.html';
import WhiteLabelLinks from "../../../common/services/setup/white-label-links.service";

export default angular.module('deskworks.whiteLabelLinksDirective', [
  'autoSave',
  WhiteLabelLinks.name,
])

  .directive('whiteLabelLinks', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'WhiteLabelLinksCtrl as $ctrl',
    };
  })

  .controller('WhiteLabelLinksCtrl', function($scope, $rootScope, dwAlerts, helper, WhiteLabelLinks, autoSave) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      WhiteLabelLinks.get()
        .then(links => { $ctrl.links = links; });
    };

    /**
     * Save links handler
     */
    $ctrl.saveLinks = autoSave.wrap(saveLinks);
    function saveLinks() {
      if (helper.pointFirstInvalid('#wlLinks') || !$ctrl.links) return;

      $ctrl.spinLinks = true;
      return WhiteLabelLinks.save($ctrl.links)
        .then(() => { $rootScope.wlLinks = $ctrl.links; }) // update current links
        .catch(err => dwAlerts.error(err, 'Failed to save white label links.'))
        .finally(() => { $ctrl.spinLinks = false; });
    }

    /**
     * Upload Terms Of Service handler
     * @param $files - files to upload
     * @param $invalidFiles - files rejected by ngfPattern directive
     */
    $ctrl.uploadTermsOfService = ($files, $invalidFiles) => {
      if ($invalidFiles && $invalidFiles.length > 0)
        return dwAlerts.error($translate.instant('WHITE_LABEL_SETTINGS.INVALID_FORMAT_PDF'));

      if ($files && $files.length > 0) {
        $ctrl.spinTOS = true;

        return WhiteLabelLinks.uploadTermsOfService($files[0])
          .catch(err => dwAlerts.error(err, 'Failed to upload terms of service.'))
          .then(result => {
            $ctrl.links.tosLinkUrl = result.data.termsOfService;
            return WhiteLabelLinks.save($ctrl.links)
              .catch(err => dwAlerts.error(err, 'Failed to save link to uploaded terms of service'));
          })
          .finally(() => { $ctrl.spinTOS = false; });
      }
    };

    /**
     * Upload Reservation Agreement handler
     * @param $files - files to upload
     * @param $invalidFiles - files rejected by ngfPattern directive
     */
    $ctrl.uploadReservationAgreement = ($files, $invalidFiles) => {
      if ($invalidFiles && $invalidFiles.length > 0)
        return dwAlerts.error($translate.instant('WHITE_LABEL_SETTINGS.INVALID_FORMAT_PDF'));

      if ($files && $files.length > 0) {
        $ctrl.spinRA = true;

        return WhiteLabelLinks.uploadReservationAgreement($files[0])
          .catch(err => dwAlerts.error(err, 'Failed to upload reservation agreement.'))
          .then(result => {
            $ctrl.links.reservationAgreementLinkUrl = result.data.reservationAgreement;
            return WhiteLabelLinks.save($ctrl.links)
              .catch(err => dwAlerts.error(err, 'Failed to save link to uploaded reservation agreement'));
          })
          .finally(() => { $ctrl.spinRA = false; });
      }
    };
  });
