const logoUrl = '/white-label/images/logo-print.png';

/**
 * Logo service - handles logos for invoice
 */
export default angular.module('deskworks.logo', [])

  .factory('logo', function($window, $http, $q, deskworksConfig) {
    let siteLogoPromise, siteLogoInvPromise;

    const maxWidth = 480, maxHeight = 171; // according to w = 140, h = 50 in invoice-header.service.js

    return {
      getSiteLogo: function() {
        siteLogoPromise = siteLogoPromise || loadLogo();
        return siteLogoPromise;
      },
      getSiteLogoInv: function() {
        siteLogoInvPromise = siteLogoInvPromise || loadLogoInv();
        return siteLogoInvPromise;
      },
      resetSiteLogo: function() {
        siteLogoPromise = null;
      },
      resetSiteLogoInv: function() {
        siteLogoInvPromise = null;
      },
    };

    /**
     * Loads non-inverted logo
     */
    function loadLogo() {
      return $q(resolve => {
        let img = new Image();
        img.crossOrigin = 'anonymous';
        img.src = deskworksConfig.getApiAbsUrl(logoUrl);

        img.onerror = function() {
          console.warn('Report failed to load site logo.');
          resolve(null);
          img = null;
        };

        img.onload = function() {
          resolve(img);
          img = null;
        };
      })
        .then(convertLogo);
    }

    /**
     * Loads inverted logo
     */
    function loadLogoInv() {
      return $q(resolve => {
        const img = document.querySelector('.sidebar-logo a img');
        if (!img) console.warn('Report failed to load site logo.');
        resolve(img);
      })
        .then(convertLogo);
    }

    /**
     * Downscales logo and converts to data-url then returns { width, height, data }
     */
    function convertLogo(img) {
      if (!img) return null;

      const dw = img.width / maxWidth;
      const dh = img.height / maxHeight;
      const scaleFactor = dw > dh ? dw : dh;
      const width = Math.round(img.width / scaleFactor);
      const height = Math.round(img.height / scaleFactor);

      const canvas = document.createElement('canvas');
      document.body.appendChild(canvas);
      canvas.width = width;
      canvas.height = height;

      const ctx = canvas.getContext('2d');

      ctx.drawImage(img, 0, 0, width, height);

      const data = canvas.toDataURL('image/png');
      document.body.removeChild(canvas);

      return { width, height, data };
    }
  });
