import ffColor from 'shared/ff-color/ff-color.directive';
import WhiteLabelVars from '../services/white-label-vars.service';
import whiteLabelVarsList from '../services/white-label-vars-list.service';
import whiteLabelImagesDirective from './white-label-images.directive';
import whiteLabelLinksDirective from './white-label-links.directive';
import whiteLabelFontsDirective from './white-label-fonts.directive';
import './white-label-settings.scss';

export default angular.module('deskworks.WhiteLabelSettingsCtrl', [
  ffColor.name,
  WhiteLabelVars.name,
  whiteLabelVarsList.name,
  whiteLabelImagesDirective.name,
  whiteLabelLinksDirective.name,
  whiteLabelFontsDirective.name,
  'deskworks.whitelabel',
])

  .controller('WhiteLabelSettingsCtrl', function($scope, $translate, dwAlerts, state, whitelabel, helper,
                                                 WhiteLabelVars, whiteLabelVarsList) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('WHITE_LABEL_SETTINGS.PAGE_TITLE'));

    /**
     * onInit
     */
    (function onInit() {
      $ctrl.vars = angular.copy(whiteLabelVarsList);
      $ctrl.vars.forEach(v => {
        v.value = deserializeValue(v, null); // init with empty values
      });

      WhiteLabelVars.get()
        .catch(err => dwAlerts.error(err, 'Failed to get white label vars'))
        .then(vars => {
          $ctrl.initialVars = {};
          $ctrl.vars.forEach(v => {
            v.value = deserializeValue(v, vars[v.id]); // re-init with values
            $ctrl.initialVars[v.id] = v.value;
          });
        });

      $scope.$watchCollection(() => $ctrl.vars.map(v => v.value), onVarsChange);
    })();

    /**
     * onVarsChange
     */
    function onVarsChange(newVals, oldVals) {
      newVals.forEach((val, idx) => {
        const v = $ctrl.vars[idx];
        const value = serializeValue(v);
        whitelabel.setProperty(`--${v.id}`, value);
        if (v.type === 'font' && value !== 'Optima' && !document.querySelector(`link[font-name="${value}"]`)) {
          addFontLink(value); // add link element for this font
        }
        if (val !== oldVals[idx] && angular.isDefined(oldVals[idx]) && $ctrl.initialVars && val !== $ctrl.initialVars[v.id]) {
          $ctrl.form.$setDirty();
        }
      });
    }

    /**
     * Add link element for new font
     * @param fontName
     */
    function addFontLink(fontName) {
      if (fontName === 'Optima') return;
      const link = angular.element(`<link rel="stylesheet" font-name="${fontName}">`);
      link[0].href = `https://fonts.googleapis.com/css2?family=${fontName}:wght@300;400;700`;
      document.head.appendChild(link[0]);
    }

    /**
     * Type comparator for filtering numbers
     * @param v - variable
     * @returns {boolean}
     */
    $ctrl.isTypeNumber = (v) => v.type === 'px' || v.type === '%';

    /**
     * Save
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#whiteLabelSetup')) return;
      const data = {};
      $ctrl.vars.forEach(v => {
        data[v.id] = serializeValue(v);
      });

      return WhiteLabelVars.save(data)
        .catch(err => dwAlerts.error(err, 'Failed to save white-label vars'))
        .then(() => {
          $ctrl.initialVars = {};
          $ctrl.vars.forEach(v => { $ctrl.initialVars[v.id] = v.value; });
          $ctrl.form.$setPristine();
        });
    };

    /**
     * Reset
     */
    $ctrl.reset = () => {
      $ctrl.vars.forEach(v => {
        v.value = $ctrl.initialVars[v.id];
      });
      $ctrl.form.$setPristine();
    };

    /**
     * Deserialize value
     */
    function deserializeValue(v, value) {
      value = value || whitelabel.getProperty(`--${v.id}`);
      if (v.type === 'px') return parseInt(value);
      if (v.type === '%') return parseInt(value);
      if (v.type === 'bool') return value === 'true';
      return value;
    }

    /**
     * Serialize value
     */
    function serializeValue(v, value) {
      value = angular.isDefined(value) ? value : v.value;
      if (angular.isDefined(v.default) && (angular.isUndefined(value) || value === null)) value = v.default;
      if (v.type === 'px') return `${value}px`;
      if (v.type === '%') return `${value}%`;
      if (v.type === 'bool') return `${value}`;
      return value;
    }
  });
