import template from './edit-image.tpl.html';
import EditImageCtrl from './edit-image.controller';
import dwModal from 'shared/dw-modal/dw-modal.service';
import dwModalDirective from 'shared/dw-modal/dw-modal.directive';

export default angular.module('editImage', [
  EditImageCtrl.name,
  dwModal.name,
  dwModalDirective.name,
])

  .factory('editImage', dwModal => (scope, inputs) =>
    dwModal.show({
      template,
      scope,
      inputs,
      controller: 'EditImageCtrl as $ctrl',
    })
      .then(modal => modal.close)
  );
