export default angular.module('deskworks.GoogleFonts', [
  'ngResource',
  'deskworks.config',
])

  .factory('GoogleFonts', function($resource, deskworksConfig) {
    const res = $resource('https://fonts.googleapis.com/css2', {}, {
      get: { method: 'GET', withCredentials: false },
    });

    return {
      get: ({ family }) => res.get({ family }).$promise,
    };
  });
