import './edit-image.scss';
import ffCrop from './ff-crop.directive';

export default angular.module('EditImageCtrl', [ffCrop.name])

  .controller('EditImageCtrl', function($scope, close, load, save, style) {
    const $ctrl = this;

    $scope.close = close;
    $ctrl.style = style;

    /**
     * Init
     */
    load()
      .then(image => {
        $ctrl.image = image;
        $ctrl.image.url += '?' + Date.now(); // TODO: maybe cache-busting is not necessary
        $ctrl.initialCrop = angular.copy($ctrl.image.crop);
      });

    /**
     * isPristine
     */
    $ctrl.isPristine = () => angular.equals($ctrl.initialCrop, $ctrl.image.crop);

    /**
     * Reset
     */
    $ctrl.reset = () => {
      $ctrl.image.crop = angular.copy($ctrl.initialCrop);
    };

    /**
     * Save
     */
    $ctrl.save = () => save($ctrl.image)
      .then(() => {
        $ctrl.initialCrop = angular.copy($ctrl.image.crop);
        return close(true);
      });
  });
