export default angular.module('deskworks.WhiteLabelImageMeta', [
  'ngResource',
  'deskworks.config',
])

  .factory('WhiteLabelImageMeta', function($resource, $timeout, deskworksConfig, Upload) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/white-label/image-meta/:imageId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: imageId => res.get({ imageId }).$promise,
      save: (imageId, data) => res.update({ imageId }, data).$promise,
      upload: (imageId, file) => Upload.upload({
        url: deskworksConfig.getApiAbsUrl(`/white-label/image-meta/${imageId}`),
        method: 'PUT',
        withCredentials: true,
        data: { file },
      }),
    };
  });
