import template from './white-label-images.tpl.html';
import WhiteLabelImageMeta from '../services/white-label-image-meta.service';
import whiteLabelImageList from '../services/white-label-image-list.service';
import editImage from 'shared/edit-image/edit-image.service';
import logo from 'shared/reports/services/logo.service';

export default angular.module('deskworks.whiteLabelImagesDirective', [
  editImage.name,
  WhiteLabelImageMeta.name,
  whiteLabelImageList.name,
  logo.name,
])

  .directive('whiteLabelImages', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'WhiteLabelImagesCtrl as $ctrl',
    };
  })

  .controller('WhiteLabelImagesCtrl', function($scope, dwAlerts, editImage, WhiteLabelImageMeta, whiteLabelImageList, logo) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.images = angular.copy(whiteLabelImageList);
    };

    /**
     * Edit image
     */
    $ctrl.editImage = image => {
      return editImage($scope, { load, save, style: image.id === 'logo' ? { backgroundColor: 'var(--logo-bg-color)' } : null })
        .then(result => {
          if (!result) return;
          refreshImage(image);
        });

      function load() {
        return WhiteLabelImageMeta.get(image.id)
          .catch(err => dwAlerts.error(err, `Failed to get ${image.id} image meta`))
      }

      function save(data) {
        return WhiteLabelImageMeta.save(image.id, data)
          .catch(err => dwAlerts.error(err, `Failed to save ${image.id} image meta`))
      }
    };

    /**
     * Upload image
     */
    $ctrl.uploadImage = (img, $files, $invalidFiles) => {
      if ($invalidFiles && $invalidFiles.length) return dwAlerts.error($translate.instant('GLOBAL.INVALID_IMAGE_FORMAT'));
      if (!$files || !$files.length) return;

      img.$$spin = true;
      return WhiteLabelImageMeta.upload(img.id, $files[0])
        .finally(() => { img.$$spin = false; })
        .catch(err => dwAlerts.error(err, `Failed to upload ${img.id} image.`))
        .then(result => {
          refreshImage(img);
          return result.data;
        });
    };

    /**
     * Refresh image
     */
    function refreshImage(img) {
      const now = Date.now();
      img.url = img.url.split('?')[0] + '?' + now; // refresh image

      document.querySelectorAll('.white-label-prefix')
        .forEach(img => {
          if (img.src) img.src = img.src.split('?')[0] + '?' + now; // refresh image
          if (img.href) img.href = img.href.split('?')[0] + '?' + now; // refresh image
        });

      logo.resetSiteLogo();
      logo.resetSiteLogoInv();
    }
  });
