import Picker from 'vanilla-picker';
import './ff-color.scss';

/**
 * ffColor directive - wrapper for 3rd party vanilla-picker
 */
export default angular.module('ffColor', [])

  .directive('ffColor', function($timeout) {
    return {
      template: '<div></div>',
      restrict: 'E',
      scope: true,
      controller: 'ffColorCtrl as $ctrl',
      bindToController: {
        ffModel: '=',
      },

      link: function($scope, $element, $attrs, $ctrl) {
        let ownUpdate = false;

        $element.addClass('ff-control');
        const picker = new Picker({
          parent: $element[0],
          editor: true,
          color: $ctrl.ffModel,
          onChange: color => $timeout(() => {
            ownUpdate = true;
            onChange(color);
          }),
        });

        function onChange(color) {
          $ctrl.ffModel = color.rgbaString;
          $element.css('color', color.rgbaString);
        }

        $scope.$watch('$ctrl.ffModel', val => {
          if (!ownUpdate) picker.setColor(val);
          ownUpdate = false;
        });
      },
    };
  })

  .controller('ffColorCtrl', function() {});
