import Jcrop from 'jcrop';
import 'jcrop/dist/jcrop.css';

export default angular.module('ffCrop', [])

  .directive('ffCrop', function($timeout) {
    return {
      restrict: 'A',
      scope: true,

      link: function($scope, $element, $attrs, $ctrl) {
        const img = $element[0].querySelector('img');
        let jcrop;

        const unwatch = $scope.$watch($attrs.ffCrop, options => {
          if (!options) return;
          unwatch(); // watch once

          $scope.$watchCollection(() => [img.getAttribute('src'), options && options.crop], () => {
            if (!$attrs.ffSrc) return;

            Jcrop.load(img)
              .then(() => $scope.$apply(() => $timeout(() => {
                if (!jcrop) jcrop = Jcrop.attach($element[0], options);

                let { x, y, w, h } = options.crop;
                const dx = img.width / img.naturalWidth;
                const dy = img.height / img.naturalHeight;
                const rect = Jcrop.Rect.create(x * dx, y * dy, w * dx, h * dy);

                if (jcrop.active) {
                  jcrop.active.render(rect);
                } else {
                  jcrop.newWidget(rect);
                  jcrop.active.listen('crop.update', (widget) => {
                    $scope.$apply(() => {
                      const dx = img.naturalWidth / img.width;
                      const dy = img.naturalHeight / img.height;

                      const rect = widget.pos.normalize();
                      rect.x *= dx; rect.y *= dy;
                      rect.w *= dx; rect.h *= dy;
                      Object.assign(options.crop, rect.round());
                    });
                  });
                }
              }, 100)));
          });

        });

        $scope.$on('$destroy', () => {
          if (jcrop) {
            jcrop.removeWidget(jcrop.active);
            jcrop.active = null;
            jcrop.destroy();
          }
          jcrop = null;
        });
      },
    };
  });
