export default angular.module('deskworks.WhiteLabelVars', [
  'ngResource',
  'deskworks.config',
])

  .factory('WhiteLabelVars', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/white-label/vars'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: () => res.get().$promise,
      save: vars => res.update({}, angular.extend({ 'dynamic-white-label': 1 }, vars)).$promise,
    };
  });
