export default angular.module('deskworks.whiteLabelImageList', [
  'deskworks.config',
])

  .factory('whiteLabelImageList', function(deskworksConfig) {
    return [
      {
        id: 'logo', _name: 'WHITE_LABEL_SETTINGS.LOGO', _hint: 'WHITE_LABEL_SETTINGS.LOGO.HINT',
        url: 'logo.png', style: { maxHeight: '130px', maxWidth: '200px', width: 'auto', backgroundColor: 'var(--logo-bg-color)' },
      },
      {
        id: 'logo-print', _name: 'WHITE_LABEL_SETTINGS.LOGO_PRINT', _hint: 'WHITE_LABEL_SETTINGS.LOGO_PRINT.HINT',
        url: 'logo-print.png', style: { maxHeight: '130px', maxWidth: '200px', width: 'auto' },
      },
      {
        id: 'icon', _name: 'WHITE_LABEL_SETTINGS.ICON', _hint: 'WHITE_LABEL_SETTINGS.ICON.HINT',
        url: 'icon-512x512.png', style: { maxHeight: '130px', maxWidth: '130px', width: 'auto' },
      },
      {
        id: 'banner', _name: 'WHITE_LABEL_SETTINGS.BANNER', _hint: 'WHITE_LABEL_SETTINGS.BANNER.HINT',
        url: 'banner.jpg', style: { maxHeight: '130px', width: 'auto' },
      },
    ]
      .map(img => {
        img.url = deskworksConfig.getApiAbsUrl(`/white-label/images/${img.url}`);
        return img;
      });
  });
